* {
  margin: 0;
  padding: 0;
 
}

.wrapper {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #5dafb8;
  color:white;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.card {
  align-items: center;
  height: 90vh;
  border-radius: 13px;
  box-shadow: 20px 40px 33px rgba(0,0,0,0.3);
  padding: 2rem;
  width: 60vw;
  background-color:  #6cc2ce;

}

.object-canvas {
  width: 56vw;
  height: 75vh;
  background:white;
  border-radius: 0px;
  margin-bottom:20px

}

.button-column {
position: fixed;
top: 73vh;
left: 7vw;
width: 180px;
height: 200px;
display: flex;
flex-direction: column;
align-items: center;
justify-content: space-around;
gap: 20px;
border-radius: 13px;
background-color:  #6cc2ce;
box-shadow: 20px 40px 33px rgba(0,0,0,0.3);


}

.button1 {
  height: 60px;
  width : 150px;
  border-radius: 13px;
  font-size: large;
  background-color:  #6cafce;
  color:white;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.colors {
display:flex;
flex-direction:row;
}

.colors div {padding:8px;}
